<template>
	<div class="transaction-detail-list">
		<mt-cell class="card-number" title="卡号">
  			<span class="number">123456789</span>
		</mt-cell>
        <!-- <better-scroll v-if="list.length > 0" :pullup="true" class="load-top">
            <div class="detail-list">
                <mt-cell v-for="item in list" :key="item" @click.native="toDetail" :title="item.type" :label="item.time" class="list-item" is-link>
                    <p class="item-p">
                        <span :class="{'money-add': item.money1 >= 0, 'money-reduce': item.money1 < 0}">{{item.money1 < 0 ? '' : '+' }}{{item.money1 | formatNumber(2)}}</span><br>
                        <span class="balance">余额：{{item.money2 | formatNumber(2)}}元</span>
                    </p>
                </mt-cell>
            </div>
        </better-scroll> -->
        <div class="load-top no-record">
            <div class="bg">
                <div class="bg-pic"></div>
                <p class="bg-msg">该账户下暂无交易记录</p>
            </div>
            <div class="fixed-footer">
                <mt-button @click.native="back" size="large" type="primary">返回</mt-button>
            </div>
        </div>
	</div>
</template>

<script>

import service from '@/services/bank/transaction-detail';

export default {
    data() {
        return {
            list: [],
            allLoaded: false
        };
    },
    methods: {
        toDetail() {
            this.$router.push({name: 'TransactionDetailDetail'});
        },
        loadTop() {
            this.$refs.loadmore.onTopLoaded();
        },
        loadBottom() {
            this.allLoaded = true;
            this.$refs.loadmore.onBottomLoaded();
        },
        getList(account) {
        	service.getTransactionList({
        		account: account
        	}).then(data => {
        		this.list = data.data.data;
        	});
        },
        back() {
            history.back();
        }
    },
    created() {
    	this.getList(this.$route.params.account);
    }
};
</script>

<style lang="scss">
.transaction-detail-list{
    height: 100%;
	.card-number{
		position: fixed;
        top:0;
        z-index: 1;
        width: 100%;
        background-size: 100% auto;
        @include image-set('../../../assets/img/bank/transaction/transaction');
        .number{
            font-size: 15px;
            color: #444857;
            letter-spacing: 4.28px;
        }
	}
	.load-top{
		padding-top: 100px;
        height: 100%;
        overflow: auto;
	}
    .list-item{
        padding: 10px 0;
    }
	.item-p{
		padding-right: 0;
		text-align: right;
	}
    .money-add{
        font-size: 20px;
        color: #ff942f;
    }
    .money-reduce{
        font-size: 20px;
        color: #46c300;
    }
	.balance{
		display: block;
        padding-top: 12px;
        font-size: 13px;
        color: #b5b5b5;
	}
    .mint-cell-title{
        .mint-cell-label{
            color: #b5b5b5;
            font-size: 13px;
        }
    }
    .mint-cell:after{
        left: 0;
    }
    .no-record{
        background-color: #fff;
        .bg{
            margin-top: 140.2px;
            text-align: center;
            .bg-pic{
                margin: auto;
                width: 177.8px;
                height: 243.8px;
                background-size: 100% 100%;
                @include image-set('../../../assets/img/bank/transaction/no-record');
            }
            .bg-msg{
                margin-top: 40px;
                font-size: 17px;
                color: #666666;
            }
        }
        .fixed-footer{
            background-color: #fff;
        }
    }
}
</style>



